<template lang="html">
  <div class="ym-wrapper">
    <div class="r-box clearfix">
        <p class="title clearfix">
        <span class="my">历史轨迹</span>
        <span class="speak">/ 说明</span>
        </p>
        <router-link class="right" tag="p" to="/WDCD">返回上一级<span class="icon iconfont icon-jiantou1"></span></router-link>
    </div>
    <div class="ditu">
      <div class="jz clearfix">
        <div class="jz-left clearfix">
          <router-link tag="p" to="/WDCD/hist/LSGJ" active-class="act">历史轨迹</router-link>
          <router-link tag="p" to="/WDCD/hist/LSLX" active-class="act">历史流向</router-link>
        </div>
        <div class="jz-right clearfix">
          <p>车牌号码: 津AW7688</p>
          <p>司机姓名: 李贵勇</p>
          <p>随车号码: 15532405231</p>
        </div>
      </div>
      <div class="ditu-map">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>

<script>
  export default{
    components:{

    }
  }
</script>

<style lang="less" scoped>
*{
  margin:0;
  padding:0;
}
.clearfix:after{
  display: block;
  clear: both;
  content: '';
}
.ym-wrapper{
.r-box{
   width: 1249px;
   float: left;
   border-left:1px solid #EEEFF3;
   padding:0 30px;
   background: #FAFBFF;
 .title{
   line-height: 20px;
   padding:36px 0 32px 0;
   float: left;
   .my{
     font-size: 20px;
     font-weight:bold;
     color: #2E52B3;
   }
   .speak{
     color: #000;
     font-size:14px;
   }
 }
 .right{
   padding-top: 36px;
   float: right;
   font-size: 12px;
   color: #ccc;
   span{
     font-size: 12px;
     padding-left: 6px;
   }
 }
}
.ditu{
  width: 1311px;
  background: #FAFBFF;
  float: left;
  .jz{
    width: 1249px;
    background: #fff;
    margin: 0 auto;
    .jz-left{
      float: left;
      p{
        width: 100px;
        height: 40px;
        float: left;
        text-align: center;
        line-height: 40px;
        border: 1px solid #3052ac;
        margin: 30px 0 0 30px;
      }
      .act{
        background: #3252a5;
        color: #fff;
      }
      p:nth-of-type(2){
        margin-left: 12px;
      }
    }
    .jz-right{
      float: right;
      padding: 0 32px;
      p{
        float: left;
        padding: 42px 10px 0 10px;
      }
    }
  }
}
}
</style>



